"use client";
import { ArrowRight, X } from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import { useEffect, useState, useRef } from "react";

export interface Card {
  id: number;
  title: string;
  imageSrc: string;
  overlayImageSrc: string;
  theme?: "teal" | "orange";
  description: string;
}

interface OverlayCardProps {
  cards: Card[];
  selectedCardId: number | null;
  setSelectedCardId: (id: number | null) => void;
  divRef: React.RefObject<HTMLDivElement | null>;
}

const OverlayCard = ({
  cards,
  selectedCardId,
  setSelectedCardId,
}: OverlayCardProps) => {
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const [startX, setStartX] = useState(0);
  const [scrollLeft, setScrollLeft] = useState(0);

  // const handleClick = () => {
  //   setTimeout(() => {
  //     divRef.current?.scrollIntoView({ behavior: "smooth", block: "center" });
  //   }, 100);
  // };

  useEffect(() => {
    const handleEsc = (e: KeyboardEvent) => {
      if (e.key === "Escape") setSelectedCardId(null);
    };

    window.addEventListener("keydown", handleEsc);
    return () => window.removeEventListener("keydown", handleEsc);
  }, [setSelectedCardId]);

  // Touch drag handlers (mobile)
  const handleTouchStart = (e: React.TouchEvent) => {
    if (selectedCardId) return;
    setStartX(
      e.touches[0].pageX - (scrollContainerRef.current?.offsetLeft || 0)
    );
    setScrollLeft(scrollContainerRef.current?.scrollLeft || 0);
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    if (selectedCardId) return;
    const x =
      e.touches[0].pageX - (scrollContainerRef.current?.offsetLeft || 0);
    const walk = (x - startX) * 1.5;

    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollLeft = scrollLeft - walk;
    }
  };

  return (
    <div
      ref={scrollContainerRef}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      className={`w-full flex flex-row  justify-start xl:justify-center items-center  gap-2 xl:gap-6
        ${selectedCardId ? "" : "overflow-x-scroll overflow-y-hidden"}
        ${selectedCardId ? "" : "snap-x snap-mandatory xl:snap-none"}
        scrollbar-hide
        pl-6 pr-6 xl:px-0 
      `}
      style={{ scrollbarWidth: "none", msOverflowStyle: "none" }}
    >
      <style jsx>{`
        div::-webkit-scrollbar {
          display: none;
        }
      `}</style>

      {cards.map((card, index) => (
        <Link
          href={index === cards.length - 1 ? "" : `/products?theme=${card.theme || "teal"}`}
          key={card.id}
          className={`${
            selectedCardId === card.id
              ? `absolute ease-in-out xl:ease-out transition-all duration-500 xl:duration-300 inset-0 xl:left-1/2 xl:-translate-x-1/2 top-0 xl:right-auto xl:bottom-auto w-[85vw] xl:w-[90%] h-full z-10 mx-auto px-6  xl:p-16 text-left flex flex-col xl:flex-row items-center gap-16 justify-center xl:justify-between text-white cursor-pointer rounded-2xl`
              : `relative rounded-2xl border-2 border-[#20695F] h-[40vh] text-white text-left flex-shrink-0 w-[calc(85vw-30px)] xl:w-full xl:flex-1 snap-start`
          }
          ${
            selectedCardId && selectedCardId !== card.id
              ? "opacity-0 ease-out transition-all duration-500"
              : ""
          }`}
        >
          {/* Card Image */}
          <Image
            className={`rounded-2xl object-cover ${
              selectedCardId === card.id
                ? "pointer-events-none -z-2 inset-0 absolute"
                : "w-full h-full"
            }`}
            src={card.imageSrc}
            fill={selectedCardId === card.id}
            height={selectedCardId === card.id ? undefined : 300}
            width={selectedCardId === card.id ? undefined : 350}
            alt="image"
          />

          {/* Card Text */}
          <div
            className={`  ${
              selectedCardId === card.id
                ? "text-center xl:text-left xl:w-[45%]"
                : "absolute bottom-0 z-10 left-0 p-4"
            }`}
          >
            <h2
              className={`${
                selectedCardId === card.id
                  ? "text-2xl xl:text-5xl"
                  : "text-sm xl:text-lg "
              }`}
            >
              {card.title}
            </h2>
            <h3
              className={`font-light flex items-center justify-between gap-2  ${
                selectedCardId === card.id
                  ? "text-xs xl:text-lg"
                  : "text-xs xl:text-sm "
              }`}
            >
              {card.description}
              {!selectedCardId && (
                <button className="border border-white rounded-full">
                  <ArrowRight
                    size={24}
                    className="-rotate-45 p-1 text-white bg-black/30 backdrop-blur-sm rounded-full  flex items-center justify-center"
                  />
                </button>
              )}
            </h3>

            {selectedCardId && (
              <button
                className="text-sm mt-5 hidden xl:flex font-normal bg-transparent rounded-full  outline-1 outline-white w-36 py-[6px]  items-center justify-center gap-2"
                type="button"
              >
                <span>Explore</span>
                <ArrowRight size={16} color="#fff" />
              </button>
            )}
          </div>

          {/* Overlay Image (Expanded State) */}
          <Image
            className={`transition-all  ease-in-out ${
              selectedCardId === card.id
                ? "opacity-100 scale-100 delay-100 duration-700 pointer-events-auto"
                : "opacity-0 scale-95 pointer-events-none"
            }`}
            src={card.overlayImageSrc}
            width={430}
            height={320}
            alt="overlay"
          />

          {selectedCardId === card.id ? (
            <>
              <div className="absolute z-[-1] inset-0 bg-black/60 backdrop-blur-xl pointer-events-none rounded-2xl"></div>
              <span
                onClick={(e) => {
                  e.stopPropagation();
                  setSelectedCardId(null);
                }}
                className="absolute  top-6 right-6 xl:top-16 xl:right-16 text-white text-2xl/6 text-center cursor-pointer z-50"
              >
                <X size={24} color="#fff" />
              </span>
              <button
                className="text-sm absolute bottom-6 xl:hidden font-normal bg-transparent rounded-full  outline-1 outline-white w-36 py-[6px] flex items-center justify-center gap-2"
                type="button"
              >
                <span>Explore</span>
                <ArrowRight size={16} color="#fff" />
              </button>
            </>
          ) : (
            <div
              style={{
                background:
                  "linear-gradient(180deg, rgba(255,255,255,0) 53%, rgba(81,81,81,0.58) 68.42%, rgba(0,0,0,0.88) 79.98%, #000000 96.15%)",
              }}
              className="absolute z-1 rounded-2xl inset-0"
            ></div>
          )}
        </Link>
      ))}
    </div>
  );
};

export default OverlayCard;
