"use client";
import { CARDS } from "@/constants/constants";
import Image from "next/image";
import { useState, useRef } from "react";

const Cards = () => {
  const [hoveredCard, setHoveredCard] = useState<number | null>(1);
  const scrollRef = useRef<HTMLDivElement>(null);

  const scrollToCard = (index: number) => {
    const container = scrollRef.current;
    if (!container) return;

    const cardWidth = window.innerWidth * 0.9;
    container.scrollTo({
      left: cardWidth * index,
      behavior: "smooth",
    });
  };

  return (
    <>
      <div className="">
        <div className="md:hidden overflow-hidden">
          <div
            ref={scrollRef}
            className="flex overflow-x-scroll snap-x snap-mandatory scrollbar-hide"
            style={{ scrollbarWidth: "none", msOverflowStyle: "none" }}
          >
            <style jsx>{`
              div::-webkit-scrollbar {
                display: none;
              }
            `}</style>
            {CARDS.map((card, index) => (
              <div
                key={card.id}
                onClick={() => scrollToCard(index)}
                className={`flex-shrink-0 snap-start
                ${card.id === 1 ? "pl-0" : "pl-2"}
                `}
                style={{ width: "calc(90%)" }}
              >
                <div className="relative overflow-hidden rounded-2xl h-[34svh]">
                  <div className="absolute inset-0">
                    <Image
                      src={card.image}
                      alt={card.title}
                      fill
                      className="object-cover"
                    />
                  </div>

                  <div
                    style={{
                      background:
                        "linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0.8) 60%, #000000 80%)",
                    }}
                    className="absolute inset-0"
                  />
                  <div className="relative z-10 text-white h-full text-left flex flex-col justify-end p-4">
                    <h3 className="text-sm">{card.title}</h3>
                    <h4 className="text-xs font-light mt-1">
                      {card.description}
                    </h4>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        <div className="hidden md:flex justify-between gap-2.5">
          {CARDS.map((card) => (
            <div
              key={card.id}
              onMouseEnter={() => setHoveredCard(card.id)}
              onMouseLeave={() => setHoveredCard(1)}
              onClick={() => setHoveredCard(card.id)}
              className={`relative overflow-hidden rounded-2xl h-[70vh] min-h-[500px] transition-all duration-700 ease-in-out ${
                hoveredCard === card.id
                  ? "w-[calc(37.5%-0.5rem)]"
                  : "w-[calc(12.5%-0.5rem)]"
              }`}
            >
              <div className="absolute inset-0 transition-all duration-700 ease-in-out">
                <Image
                  src={card.image}
                  alt={card.title}
                  fill
                  className="object-cover"
                />
              </div>

              <div
                style={{
                  background:
                    hoveredCard === card.id
                      ? "linear-gradient(180deg, rgba(255, 255, 255, 0) 53%, rgba(81, 81, 81, 0.578558) 68.42%, rgba(0, 0, 0, 0.88) 79.98%, #000000 96.15%)"
                      : "rgba(0, 0, 0, 0.45)",
                }}
                className="absolute inset-0 transition-all duration-700 ease-in-out"
              />

              <div
                className={`relative z-10 text-left text-white h-full flex flex-col p-6 transition-all duration-1000 delay-75 ease-in-out ${
                  hoveredCard === card.id
                    ? "justify-end"
                    : "-rotate-90 items-center justify-center"
                }`}
              >
                <h3
                  className={`text-lg ${
                    hoveredCard === card.id ? "w-full" : "whitespace-nowrap"
                  }`}
                >
                  {card.title}
                </h3>

                <h4
                  className={`text-sm font-light transition-all ease-in-out ${
                    hoveredCard === card.id
                      ? "opacity-100 delay-1000 duration-1000"
                      : "duration-75 opacity-0 h-0"
                  }`}
                >
                  {card.description}
                </h4>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Cards;
