"use client";

import LiteYouTubeEmbed from "react-lite-youtube-embed";

const BentoGrid = () => {
  const instagramLinks = [
    "https://www.instagram.com/p/DSZ_UQDgYQi/?igsh=MTNsand3d3kybGZ6ag==",
    "https://www.instagram.com/p/DRmFsKqjxUx/?img_index=2&igsh=MTU5YWFkbWtmcHJhbQ==",
    "https://www.instagram.com/reel/DRcY3JCDa_w/?igsh=MWdkNGRudWR4ZmV2MQ==",
    "https://www.instagram.com/p/DPf0j7bDgzk/?igsh=d2s2dmpyMG1lMXU4",
    "https://www.instagram.com/reel/DQWWNufEviO/?igsh=MXRsc3AwZnkyaHpp",
  ];

  const youtubeLinks = [
    "https://youtu.be/jLZFhsaMkOg?si=mxk6qcKrkZ0NDFg0",
    "https://youtu.be/Z1EYKoZx-PM?si=U6u-8TJSJPkJ269P",
    "https://youtu.be/_rcNVMcP0cI?si=elAHxVnMxuCgEuC4",
  ];

  function extractYouTubeId(link: string): string | null {
    try {
      const url = new URL(link);
      if (url.hostname === "youtu.be") {
        return url.pathname.slice(1).split("?")[0];
      }
      if (url.hostname.includes("youtube.com")) {
        return url.searchParams.get("v");
      }
      return null;
    } catch {
      return null;
    }
  }

  function extractYouTubeParams(link: string): string {
    try {
      const url = new URL(link);
      const si = url.searchParams.get("si");
      return si ? si : "";
    } catch {
      return "";
    }
  }

  function instagramLinkToEmbed(link: string) {
    try {
      const url = new URL(link);
      const match = url.pathname.match(/\/(p|reel|tv)\/([^/]+)/);

      if (!match) throw new Error("Invalid Instagram link");

      const postId = match[2];

      return (
        <iframe
          src={`https://www.instagram.com/${match[1]}/${postId}/embed`}
          className="w-full h-full object-cover rounded-2xl"
          frameBorder="0"
          loading="lazy"
        />
      );
    } catch {
      return null;
    }
  }

  return (
    <div className="py-8 px-6 md:px-16">
      <div className="hidden md:grid grid-cols-6 auto-rows[66px] gap-4">
        <div className="col-span-3 row-span-5 rounded-4xl">
          <div className="relative w-full h-full aspect-video rounded-4xl overflow-hidden">
            <LiteYouTubeEmbed
              id={extractYouTubeId(youtubeLinks[0]) || ""}
              title="YouTube video"
              poster="maxresdefault"
              webp={true}
              activatedClass="lty-activated"
              lazyLoad={true}
              params={extractYouTubeParams(youtubeLinks[0])}
            />
          </div>
        </div>

        <div className="col-span-3 row-span-5">
          <div className="relative w-full h-full rounded-2xl overflow-hidden aspect-video">
            <LiteYouTubeEmbed
              id={extractYouTubeId(youtubeLinks[2]) || ""}
              title="YouTube video"
              poster="maxresdefault"
              webp={true}
              activatedClass="lty-activated"
              lazyLoad={true}
              params={extractYouTubeParams(youtubeLinks[0])}
            />
          </div>
        </div>

        <div className="col-span-4 row-span-4">
          <div className="relative w-full rounded-4xl h-full aspect-video overflow-hidden">
            <LiteYouTubeEmbed
              id={extractYouTubeId(youtubeLinks[1]) || ""}
              title="YouTube video"
              poster="maxresdefault"
              webp={true}
              activatedClass="lty-activated"
              lazyLoad={true}
              params={extractYouTubeParams(youtubeLinks[1])}
            />
          </div>
        </div>

        <div className="col-span-2 row-span-4">
          <div className="relative w-full h-full aspect-video">
            {instagramLinkToEmbed(instagramLinks[1])}
          </div>
        </div>

        <div className="col-span-2 row-span-5">
          <div className="relative w-full h-full aspect-[3/4]">
            {instagramLinkToEmbed(instagramLinks[2])}
          </div>
        </div>

        <div className="col-span-2 row-span-5">
          <div className="relative w-full h-full">
            {instagramLinkToEmbed(instagramLinks[3])}
          </div>
        </div>

        <div className="col-span-2 row-span-5">
          <div className="relative w-full h-full">
            {instagramLinkToEmbed(instagramLinks[4])}
          </div>
        </div>
      </div>

      <div className="md:hidden flex flex-col gap-2">
        <div className="w-full rounded-2xl overflow-hidden">
          <LiteYouTubeEmbed
            id={extractYouTubeId(youtubeLinks[0]) || ""}
            title="YouTube video"
            poster="maxresdefault"
            webp={true}
            activatedClass="lty-activated"
            lazyLoad={true}
            params={extractYouTubeParams(youtubeLinks[0])}
          />
        </div>

        <div className="rounded-2xl overflow-hidden">
          <LiteYouTubeEmbed
            id={extractYouTubeId(youtubeLinks[1]) || ""}
            title="YouTube video"
            poster="maxresdefault"
            webp={true}
            activatedClass="lty-activated"
            lazyLoad={true}
            params={extractYouTubeParams(youtubeLinks[1])}
          />
        </div>

        <div className="rounded-2xl overflow-hidden">
          <div className="w-full rounded-2xl overflow-hidden aspect-[3/4]">
            {instagramLinkToEmbed(instagramLinks[0])}
          </div>
        </div>

        <div className="relative w-full aspect-[3/4] rounded-2xl overflow-hidden">
          {instagramLinkToEmbed(instagramLinks[3])}
        </div>
      </div>
    </div>
  );
};

export default BentoGrid;
