"use client";

import React, { useState, useRef, useEffect } from "react";
import { usePathname } from "next/navigation";
import { Search, ChevronDown } from "lucide-react";
import { useGlobalStore } from "@/lib/store";
import productFaqs, { homeFaqs } from "./Faqs.constants";
import Link from "next/link";

const Faqs = () => {
  const pathname = usePathname();
  const isHomePage = pathname === "/";
  const [selectedQuesId, setSelectedQuesId] = useState<number | null>(null);
  const [isVisible, setIsVisible] = useState(false);
  const containerRef = useRef<HTMLDivElement>(null);
  const { theme } = useGlobalStore();
  const isTeal = theme === "teal";

  const faqs = isHomePage ? homeFaqs : productFaqs[theme];

  const [filteredFaqs, setFilteredFaqs] = useState(faqs || []);
  const [searchTerm, setSearchTerm] = useState('');

  // Update filteredFaqs when theme changes
  useEffect(() => {
    if (searchTerm) {
      const lowerCasedTerm = searchTerm.toLowerCase();
      const filteredList = faqs.filter(
        faq =>
          faq.question.trim().toLowerCase().includes(lowerCasedTerm) ||
          faq.answer.trim().toLowerCase().includes(lowerCasedTerm)
      );
      setFilteredFaqs(filteredList);
    } else {
      setFilteredFaqs(faqs || []);
    }
  }, [theme, faqs, searchTerm]);

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const term = e.target.value
    setSearchTerm(term)
    const lowerCasedTerm = term.toLowerCase()
    const filteredList = faqs.filter(faq => faq.question.trim().toLowerCase().includes(lowerCasedTerm) || faq.answer.trim().toLowerCase().includes(lowerCasedTerm))
    setFilteredFaqs(filteredList)
  }

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.1 }
    );

    const currentRef = containerRef.current;

    if (currentRef) {
      observer.observe(currentRef);
    }

    return () => {
      if (currentRef) {
        observer.unobserve(currentRef);
      }
    };
  }, []);

  return (
    <div
      ref={containerRef}
      className="py-13 container mx-auto px-6 md:px-16 space-y-11"
    >
      <div className="capitalize w-full flex flex-col md:flex-row md:items-center justify-between">
        {/* left */}
        <div
          className={`transition-all duration-1000 ease-out ${
            isVisible
              ? "translate-x-0 opacity-100"
              : "-translate-x-full opacity-0"
          }`}
        >
          {isHomePage ? (
            <h2 className="text-2xl md:text-[42px]/16">
              Frequently Asked Questions{" "}
            </h2>
          ) : (
            <div>
              <h2 className="font-bold italic  text-5xl text-primary">FAQs</h2>
              <h4 className="text-xl sm:text-3xl text-foreground">
                Quick, Clear, {isTeal ? "Gut-Led" : "Weigh-Wise"}
              </h4>
            </div>
          )}
        </div>

        {/* right */}
        <div
          className={`relative w-full md:w-72 mt-4 md:mt-0 transition-all duration-1000 ease-out ${
            isVisible
              ? "translate-x-0 opacity-100"
              : " sm:translate-x-full opacity-0"
          }`}
        >
          <input
            type="text"
            onChange={handleSearch}
            value={searchTerm}
            placeholder="Search your keyword here"
            className="w-full p-3 md:p-4  pl-10 md:pl-11 text-base/5 placeholder:text-gray-400  border border-black rounded-full outline-none "
          />
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-black" />
        </div>
      </div>

      {/* bottom */}
      <div
        className={`divide-y-3 divide-[#CDD6DA]/25 transition-all duration-1000 ease-out ${
          isVisible ? "translate-y-0 opacity-100" : "translate-y-full opacity-0"
        }`}
      >
        {filteredFaqs.map((faq) => (
          <div
            onClick={() =>
              setSelectedQuesId(selectedQuesId === faq.id ? null : faq.id)
            }
            key={faq.id}
            className={`rounded-t-3xl cursor-pointer p-2 md:p-6 flex gap-4 md:gap-11 transition-all duration-1000 ease-in-out ${
              selectedQuesId === faq.id ? "bg-primary-light" : "bg-white"
            }`}
          >
            <span className="font-bold text-2xl  md:text-[40px]/12">
              0{faq.id}
            </span>

            <div className="w-full space-y-2.5">
              <div className="w-full flex items-start justify-between">
                <h2 className="text-ld md:text-2xl  ">{faq.question}</h2>
                <ChevronDown
                  className={`w-8 h-8 md:w-11 md:h-11 rounded-full p-2 flex-shrink-0 transition-transform duration-300 ease-in-out ${
                    selectedQuesId === faq.id
                      ? "bg-primary text-white rotate-180"
                      : "bg-primary-light rotate-0"
                  }`}
                />
              </div>

              <p
                className={`text-sm md:text-base text-gray-800/85 overflow-hidden transition-all duration-1000 ease-in-out ${
                  selectedQuesId === faq.id
                    ? "max-h-40 opacity-100"
                    : "max-h-0 opacity-0"
                }`}
              >
                {faq.answer}
              </p>
            </div>
          </div>
        ))}
      </div>

      <Link href={"/faq"}>
        <button className="mx-auto mt-4 px-8 py-3 text-sm md:text-lg  text-black border-1 border-black rounded-full flex items-center gap-2 transition-all duration-300 ease-in-out group hover:scale-105">
          View All FAQs
          <span className="text-2xl font-bold transition-transform duration-300 ease-in-out group-hover:-rotate-45 ">
            →
          </span>
        </button>
      </Link>
    </div>
  );
};

export default Faqs;
