"use client";
import { useGlobalStore } from "@/lib/store"
import Image from "next/image";
import { usePathname } from "next/navigation";

export default function FooterBanner({
  children,
}: {
  children?: React.ReactNode;
}) {
  const { theme } = useGlobalStore();
  const isTeal = theme === "teal";
  const path = usePathname();
  const IsProductsPage = path.startsWith("/products");
  return (
    <div className="bg-primary w-full relative mt-10 xl:py-8">
      <div className="absolute -top-[60px] xl:-top-[79px] w-full h-[80px] pb-10 ">
        <Image
          src={
            IsProductsPage && !isTeal ? "/orange-wave.png" : "/teal-wave.png"
          }
          alt="footer curve wave"
          fill
          // className=" object-contain xl:object-fill "
          className=" object-contain sm:object-fill"
        />
      </div>
      {children}
    </div>
  );
}
