"use client";
import useHorizontalScroll from "@/app/hooks/useHorizontalScroll";
import { ChevronLeft, ChevronRight } from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import { useState, useRef } from "react";

type Item = {
  id: number;
  imgSrcSmall: string;
  imgSrcLarge: string;
  heading: string;
  subheading: string;
};

export default function RadialLayout() {
  const [selectedItem, setSelectedItem] = useState<Item | null>(null);
  const centerRef = useRef<HTMLDivElement | null>(null);

  const { scroll, scrollRef } = useHorizontalScroll();

  const leftItems: Item[] = [
    {
      id: 1,
      imgSrcSmall: "/recipies/midday-fuel-sm.png",
      imgSrcLarge: "/recipies/midday-fuel-lg.png",
      heading: "Midday Fuel",
      subheading: "Power Through Noon",
    },
    {
      id: 2,
      imgSrcSmall: "/recipies/post-recovery-sm.png",
      imgSrcLarge: "/recipies/post-recovery-lg.png",
      heading: "Post-Recovery",
      subheading: "Restore With Care",
    },
    {
      id: 3,
      imgSrcSmall: "/recipies/evening-treat-sm.png",
      imgSrcLarge: "/recipies/evening-treat-lg.png",
      heading: "Evening Treat",
      subheading: "Tasty, Light, Lovely",
    },
  ];

  const rightItems: Item[] = [
    {
      id: 4,
      imgSrcSmall: "/recipies/morning-boost-sm.png",
      imgSrcLarge: "/recipies/morning-boost-lg.png",
      heading: "Morning Boost",
      subheading: "Energize Your Day",
    },
    {
      id: 5,
      imgSrcSmall: "/recipies/weekend-bliss-sm.png",
      imgSrcLarge: "/recipies/weekend-bliss-lg.png",
      heading: "Weekend Bliss",
      subheading: "Guilt-Free Joy",
    },
    {
      id: 6,
      imgSrcSmall: "/recipies/pre-workout-sm.png",
      imgSrcLarge: "/recipies/pre-workout-lg.png",
      heading: "Pre-Workout",
      subheading: "Fuel Before Move",
    },
  ];

  const handleItemClick = (item: Item) => {
    setSelectedItem(item);
  };

  const renderItem = (item: Item) => {
    const isSelected = selectedItem?.id === item.id;
    return (
      <div key={item.id} className="shrink-0">
        {/* Original position item */}
        <div
          className={` flex flex-row xl:flex-col text-center cursor-pointer transition-opacity duration-700 
            ${isSelected ? "opacity-50" : "opacity-100"}
          `}
          onClick={() => handleItemClick(item)}
        >
          <div className="space-y-1.5 xl:space-y-2.5">
            <Image
              className={`object-cover max-w-[130px] xl:max-w-[150px]  rounded-full  mx-auto ${
                isSelected ? "border-2 border-white border-dashed" : ""
              }`}
              src={item.imgSrcLarge}
              width={150}
              height={150}
              alt={"dish-" + item.id}
            />
            <div className=" text-center">
              <h2 className="text-base/6 xl:text-2xl font-normal">
                {item.heading}
              </h2>
              <h3 className="text-xs/4.5 xl:text-sm font-normal">
                {item.subheading}
              </h3>
            </div>
          </div>
        </div>
      </div>
    );
  };
  return (
    <div
      className="relative  radial-container pt-11 xl:pt-16 pb- text-white whitespacenowrap overflow-hidden
      bg-[linear-gradient(180deg,#D9EBE2_-44.15%,var(--primary)_4.22%,var(--primary)_34.46%,var(--primary)_52.45%,var(--primary)_74.54%,#588F87_98.35%,#D9EBE2_140.62%)]
      xl:bg-[linear-gradient(180deg,#D9EBE2_-44.15%,var(--primary)_19.19%,var(--primary)_34.46%,var(--primary)_52.45%,var(--primary)_74.54%,#588F87_88.18%,#D9EBE2_140.62%)]
      "
    >
      <div className="relative grid container mx-auto px-6 xl:px-16 auto-rows[180px] xl:auto-rows-auto xl:grid-cols-12 gap-6 xl:gap-20 xl:items-center">
        {/* Left Column */}
        <div
          ref={scrollRef}
          className="flex flex-row xl:flex-col items-center gap-6 sm:gap-9 xl:gap-7 row-span-1  xl:col-span-3 overflow-x-auto overflow-y-hidden xl:overflow-visible scrollbarhide"
        >
          {leftItems.map((item) => renderItem(item))}
          <div className="xl:hidden flex flex-row gap-7 ">
            {rightItems.map((item) => renderItem(item))}
          </div>

          <button
            onClick={() => scroll("left")}
            className="xl:hidden absolute left-3 top-[65px] -translate-y-1/2 bg-transparent/50 backdrop-blur-sm p-2 rounded-full shadow-lg hover:bg-white transition-colors z-10"
            aria-label="Previous image"
          >
            <ChevronLeft className="w-4 h-4 text-white" />
          </button>

          <button
            onClick={() => scroll("right")}
            className="xl:hidden absolute right-3 top-[65px] -translate-y-1/2 bg-transparent/50 backdrop-blur-sm p-2 rounded-full shadow-lg hover:bg-white transition-colors z-10"
            aria-label="Next image"
          >
            <ChevronRight className="w-4 h-4 text-white" />
          </button>
        </div>

        {/* Center Circle */}
        <div
          ref={centerRef}
          className={`overflow-hidden xl:max-w-none row-span-3 xl:row-span-1  xl:col-span-6 w-full flex text-white flex-col items-center justify-center transition-opacity duration-500 
          `}
        >
          <div className=" w-full xl:w-full xl:max-w-[550px] aspect-square rounded-full border-2 border-dashed border-white/60 flex items-center justify-center mb-8">
            {selectedItem && (
              <Image
                width={327}
                height={327}
                src={selectedItem.imgSrcLarge}
                className="xl:hidde w-full h-full object-cover"
                alt="dish image"
              />
            )}

            {!selectedItem && (
              <div
                className={`text-center px-8  
              `}
              >
                <h1 className="text-2xl xl:text-[45px]/15 ">
                  Your Favourite Dish,
                </h1>
                <h1 className="text-2xl xl:text-[45px]/15 ">
                  Served Healthier{" "}
                </h1>

                <p className="text-xs xl:text-base font-light">
                  Click on the dish to view here
                </p>
              </div>
            )}
          </div>

          <div className=" text-center mb-4 ">
            <h2 className="font-normal text-lg sm:text-[24px]/9 xl:text-[32px]/10">
              {selectedItem && selectedItem.heading}
            </h2>

            <p className="font-light text-[10px] sm:text-sm xl:text-base ">
              {selectedItem && selectedItem.subheading}
            </p>
          </div>

          <Link
            href={"/blogs"}
            className="bg-white  xl:text-lg rounded-md py-2.5 px-6 font-normal text-black hover:bg-white/95 "
          >
            Explore All Recipes
          </Link>
        </div>

        {/* Right Column */}
        <div className="hidden xl:flex xl:flex-col xl:gap-7  xl:col-span-3">
          {rightItems.map((item) => renderItem(item))}
        </div>
      </div>
    </div>
  );
}
