import { useEffect, useState } from "react";

export function useDelayedAnimation(delay: number = 1000) {
  const [animation, setAnimation] = useState(false);
  useEffect(() => {
    const timer = setTimeout(() => {
      setAnimation(true);
    }, delay);
    return () => {
      clearTimeout(timer);
    };
  }, [delay]);

  return animation;
}
